.white_content {
  display: block;
  position: fixed;
  top: 10%;
  left: 25%;
  width: 50%;
  height: 80%;
  padding: 16px;
  border: 2px solid rgb(46, 44, 40);
  background-color: white;
  z-index: 1002;
  overflow: auto;
}

.black_overlay {
  display: block;
  position: fixed;
  top: 0%;
  left: 0%;
  width: 100%;
  height: 100%;
  background-color: black;
  z-index: 1001;
  -moz-opacity: 0.8;
  opacity: .80;
  filter: alpha(opacity=80);
}


